import React from "react";

function HITSlide({ title, content, imgURL }){
    return (
        <div className="hit-slide-content" >
            <div className="hit-img-container">
                <img src={imgURL} alt="ovocado"></img>
            </div>

            <div className="hit-slide-text">
                <h1>{title}</h1>
                <p>{content}</p>
            </div>
        </div>
    );
}

export default HITSlide;