import React, { useState } from "react";
import SliderContent from "./SliderContent"

import GGArrow from "./GGArrow";
import Arrow from "./Arrow";

import GGSlide from "./GGSlide";
import Slide from "./Slide";
import HITSlide from "./HITSlide";

import ggContent from "../ggCarouselContent";
import foodImgs from "../carouselFoodImgs";
import hitContent from "../hitCarouselContent";

function Carousel(props) {
    const [ state, setState ] = useState({
        activeSlide: 0,
        translate: 0,
        transition: 0.45
    });

    const { activeSlide, translate, transition } = state;

    var content = [];
    var itemWidth = 0;
    var itemsPerPage = 0;

    if (props.section === "recipes") {
        content = [...foodImgs];
        itemWidth = 17.5;
        itemsPerPage = 4;
    } else if (props.section === "goGreen") {
        content = [...ggContent];
        itemWidth = 35;
        itemsPerPage = 1;
    } else if (props.section === "howItWorks") {
        content = [...hitContent];
        itemWidth = 67;
        itemsPerPage = 1;
    }

    function nextSlide(){
        if (props.end === "goBack") {
            if ( activeSlide === (content.length - 1)) {
                return setState({
                    ...state,
                    activeSlide: 0,
                    translate: 0
                });
            } 
        } else if (props.end === "stop") {
            if ( (activeSlide + itemsPerPage) === content.length - 1) {
                return setState({
                    ...state,
                    activeSlide: activeSlide + 1,
                    translate: ((itemWidth * content.length) - itemWidth * itemsPerPage) 
                });
            } 
        }
        
        if ( !(activeSlide === content.length - itemsPerPage) ) {
            setState({
                ...state,
                activeSlide: activeSlide + 1,
                translate: (itemWidth * (activeSlide + 1)) 
            });
        }
    }

    function prevSlide(){
        if ( !(activeSlide === 0) ) {
            setState({
                ...state,
                activeSlide: activeSlide - 1,
                translate: (itemWidth * (activeSlide - 1)) 
            });
        }
    }

    return (
        <div className="carousel">
            <div className="slider">
                <SliderContent
                    translate={translate}
                    transition={transition}
                    customWidth={itemWidth}
                    numberSlides={content.length}
                >
                {
                    content.map((item, idx) => {
                        return (
                            props.section === "recipes" ? 
                            <Slide 
                                key={idx}
                                content={item}                         
                            />
                            : props.section === "goGreen" ?
                            <GGSlide 
                                key={item.id}
                                title={item.title}
                                content={item.content}                        
                            /> 
                            : props.section === "howItWorks" ?
                            <HITSlide 
                                key={item.id}
                                imgURL={item.imgURL}
                                title={item.title}
                                content={item.content}                        
                            /> 
                            : null
                        );
                    })
                }
                </SliderContent>
                {
                    props.end === "goBack" ?
                    <GGArrow
                        direction="right"
                        handleClick={nextSlide}
                    />
                    : 
                    <div>
                        <Arrow
                            direction="right"
                            handleClick={nextSlide}
                        />
                        <Arrow
                            direction="left"
                            handleClick={prevSlide}
                        />
                    </div>
                }
                
            </div>
        </div>
    );
}

export default Carousel;