import React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faApple } from '@fortawesome/free-brands-svg-icons'

function Try() {
  return (
    <div id="try" className="try">
        <div className="try-content">
            <h1>Delicius</h1>
            <h1>Planet-friendly food</h1>
            <p>
                <span className="try-subtext">
                    Culpa qui officia deserunt mollit anim id est laborum.
                    Sed ut perspiciatis unde omnis iste natus error sit
                </span>
            </p>
            <button className="btn-try">LET'S TRY</button>

            <div className="try-download">
                <p>Download on iOS and android for free</p>
                <a href="#">
                    <div className="btn-download">
                        <FontAwesomeIcon className="apple-icon" icon={faApple}/>
                        <div className="download-button-text">
                            <span className="btn-up-text">Download on the</span>
                            <span className="btn-bottom-text">App Store</span>
                        </div>
                       
                    </div>
                </a>
                <a href="#">
                    <div className="btn-download">
                        <img className="playstore-icon" src="./imgs/google_play_icon.png" alt="playstore icon"></img>
                        <div className="download-button-text">
                            <span className="btn-up-text">GET IT ON</span>
                            <span className="btn-bottom-text">Google Play</span>
                        </div>
                    </div>
                </a>
            </div>

        </div>
        <div className="try-img-container">
            
        </div>
       

    </div>
  );
}

export default Try;